    margin-bottom: 0;
    flex: 0 1 calc(100% - 90px); }
  .personal__data {
    margin-top: 15px;
    padding-bottom: 45px; }
  .personal__tabs-header a {
    padding-left: 18px;
    padding-right: 18px; }
  .contact__item-name {
    width: 90px; }
  .contact__item {
    margin-top: 11px; }
  .my__training__title {
    font-size: 18px;
    padding-top: 18px;
    font-weight: bold;
    margin-bottom: 15px;
 }    // letter-spacing: 0.01em
  .es-progress-wrap {
    .my__training__title {
      font-size: 22px;
      padding-top: 0; } }
  .es-training__title {
    padding-left: 10px;
    font-size: 16px; }
  .es-training__body-wrap {
    padding-top: 15px;
    padding-bottom: 0px; }
  .es-training__info {
    flex-direction: column-reverse; }
  .es-training__right {
    // justify-content: flex-end
    padding-left: 10px;
    text-align: left; }
  .es-training__line {
    padding-left: 10px;
    margin-bottom: 10px; }
  .es-training__right {
