  .es-training__hidden-body .es-training__line {
    margin-bottom: 8px; }
  .es-training__box {
    margin-bottom: 20px; }



  /* grades */

  .es-progress__tab-header {
    position: relative;
    margin-left: -9px;
    margin-right: -9px;
    padding-bottom: 12px;
    &::after {
      display: inline-block;
      content: ' ';
      position: absolute;
      bottom: 0;
      left: 10px;
      right: 0;
      height: 1px;
      background: #e7e7e7; } }

  .es-progress__tab-header-item {
    margin-left: 0;
    padding-left: 9px;
    padding-right: 9px;
    margin-right: 7px;
    margin-bottom: 6px; }

  /* table */
  .es-progress__line:first-child {
    display: none; }
  .es-progress__tab-body {
    padding-top: 0; }
  .es-progress__line {
    flex-wrap: wrap;
    padding: 10px 0;
    &:last-child {
      border-bottom: 0; } }
  .es-progress__line-item {
    width: 100%;
    margin-right: 0;
    margin-bottom: 10px;
    display: flex;
    span.for-mobile {
      display: inline-block;
      width: 150px;
      flex-shrink: 0; } }

  .es-discipline {
    font-size: 14px;
    margin-bottom: 6px; }

  /* request */
  .request__content__head {
    display: block; }

  .request__content__head {
    &--with-btn {
      padding-bottom: 0; }

    .request__form__btn {
      padding: 5px 10px;
      display: block;
      width: 100%;
      max-width: 400px;
      margin-top: 10px; } }
  /* table */
  .request__list {
    margin-top: 10px;
    border-top: 1px solid #e7e7e7 {
        // &::after
        //   display: inline-block
        //   content: ' '
        //   position: absolute
        //   bottom: 0
        //   left: 10px
        //   right: 0
        //   height: 1px
 } }        //   background: #e7e7e7
  .request__list-header {
    display: none; }
  .request__list-body {}

  .request__list-line {
    flex-wrap: wrap;
    padding: 15px 0 10px;
    div {
      width: 100%;
      margin-right: 0;
      margin-bottom: 10px;
      display: flex; } }

  .request__list {
    span.for-mobile {
      display: inline-block;
      width: 150px;
      flex-shrink: 0; } }



  /* request page */
  .request__content__result {
    margin-top: 5px; }
  .es-request__content-line {
    margin-bottom: 10px; }
  .es-request__content-label {
    // width: 140px
    width: 110px;
    flex-shrink: 0; }
  .es-request__data-wrap {
    margin-top: 15px; }
  .es-request__data-wrap .es-request__content-label {
    width: 140px;
    // flex-shrink: 1
    span {
      white-space: normal;
      padding-right: 0; } }
  .es-request__data-wrap .es-request__content-line {
    border-bottom: 0;
    margin-bottom: 15px;
    align-items: flex-start; }
  .request__content__back {
    text-align: center;
    font-size: 14px; }
  .request__content__back__link {
    font-size: 14px;
    font-weight: 400; }

  /* feedback, request-new */
  .appeal__content__head, .request__content__head {
    border-bottom: 0;
    margin-bottom: 0;
    padding-bottom: 12px; }

  .appeal__form__item, .request__form__item {
    flex-wrap: wrap;
    max-width: 450px; }

  .appeal__form__label, .request__form__label {
    width: 100%;
    padding-bottom: 10px; }

  /* ads */
  .appeal__content__head_title {
